.era_switcher {
  width: 100%;
  height: 15%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;

  .first_life_switcher_normal {
    width: 17%;
    height: 50%;
    background-color: #002c88;
    border: 2px solid #f5ff00;
    border-radius: 30px;
    box-shadow: 5px 5px 10px #282626;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: #f5ff00;
    text-transform: uppercase;
    transition: 0.4s all;
    touch-action: none;
  }

  .first_life_switcher_finished {
    border: 2px solid #002c88;
    background-color: #f5ff00;
    width: 17%;
    height: 50%;
    border-radius: 30px;
    box-shadow: 5px 5px 10px #282626;
    display: none;
    justify-content: center;
    align-items: center;
    color: #00aa0c;
    text-transform: uppercase;
  }

  .mammals_switcher_locked {
    width: 17%;
    height: 50%;
