.game_out {
  height: 0;
  padding-bottom: 47%;
  position: relative;
  width: 90%;

  .game_in {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 1300px;
    height: 100%;
    max-height: 650px;
    border: 3px solid #f5ff00;
    border-radius: 25px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .instructions_left {
      position: absolute;
      background-color: rgba(40, 38, 38, 0.91);
      height: 100%;
      width: 59%;
      z-index: 15;
      border-radius: 23px 0 0 23px;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #f5ff00;


      p {
        max-width: 80%;
        font-size: 25px;
      }

      .instructions_left_next {
        width: 100px;
        height: 30px;
        background-color: #002c88;
        border: 2px solid #f5ff00;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        font-size: 16px;
        text-transform: uppercase;
        transition: 0.4s all;

        &:hover {
          background-color: #f5ff00;
          border: 2px solid #002c88;
          color: #002c88;
        }
      }
    }

    .instructions_right {
      position: absolute;
      background-color: rgba(40, 38, 38, 0.91);
      height: 100%;
      width: 41%;
      z-index: 15;
      border-radius: 0 23px 23px 0;
      top: 0;
      left: 59%;
      display: none;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #f5ff00;

      .instructions_right_story{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;


        .instructions_right_story_txt {
          width: 95%;
          display: flex;
          justify-content: space-between;
          align-items: center;


          i {
            width: 10%;
            color: #f5ff00;
            font-size: 20px;
          }

          p {
            width: 85%;
            font-size: 25px;
            margin: 0 0 5px 0;
          }
        }

        .instructions_right_story_next {
          width: 100px;
          height: 30px;
          background-color: #002c88;
          border: 2px solid #f5ff00;
          border-radius: 30px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          font-size: 16px;
          text-transform: uppercase;
          transition: 0.4s all;

          &:hover {
            background-color: #f5ff00;
            border: 2px solid #002c88;
            color: #002c88;
          }
        }
      }

      .instructions_right_era{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        visibility: hidden;

        .instructions_right_era_txt {
          width: 95%;
          display: flex;
          justify-content: space-between;
          align-items: center;


          i {
            width: 10%;
            color: #f5ff00;
            font-size: 20px;
          }

          p {
            width: 85%;
            font-size: 25px;
            margin: 0 0 5px 0;
          }
        }

        .instructions_right_era_next {
          width: 100px;
          height: 30px;
          background-color: #002c88;
          border: 2px solid #f5ff00;
          border-radius: 30px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          font-size: 16px;
          text-transform: uppercase;
          transition: 0.4s all;

          &:hover {
            background-color: #f5ff00;
            border: 2px solid #002c88;
            color: #002c88;
          }
        }
      }
    }

    .title_story {
      height: 95%;
      width: 58%;
      border: 2px solid #f5ff00;
      border-radius: 23px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;

      .title {
        margin: 5px 0 0 0;
        width: 100%;
        height: 15%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .title_main {
          font-family: 'Bangers', cursive;
          color: #f5ff00;
          font-size: 75px;
          letter-spacing: 1px;
          text-shadow: 5px 5px 10px #282626;
          -webkit-text-stroke-width: 3px;
          -webkit-text-stroke-color: #000656;
          margin: 0;
        }

        .title_foot {
          color: #f5ff00;
          font-size: 8px;
          margin: 0;

          a {
            color: #f5ff00;
            text-decoration: none;
          }
        }
      }
    }

    .era_counter_tap {
      height: 95%;
      width: 40%;
      border: 2px solid #f5ff00;
      border-radius: 23px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
}

@media (max-width: 1130px) {
  .game_out {

    .game_in {
      border: 2px solid #f5ff00;

      .instructions_right{

        .instructions_right_story{

          .instructions_right_story_txt{

            p{
              font-size: 20px;
            }
          }
        }

        .instructions_right_era{

          .instructions_right_era_txt{

            p{
              font-size: 20px;
            }
          }
        }
      }

      .title_story {

        .title {

          .title_main {
            font-size: 55px;
            text-shadow: 5px 5px 8px #282626;
          }
        }
      }
    }
  }
}

@media (max-width: 830px) {
  .game_out {

    .game_in {
      max-height: 365px;
      max-width: 710px;

      .instructions_right{

        .instructions_right_story{

          .instructions_right_story_txt{

            p{
              font-size: 16px;
            }
          }
        }

        .instructions_right_era{

          .instructions_right_era_txt{

            p{
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 800px) {
  .game_out {

    .game_in {

      .title_story {
        border: 1px solid #f5ff00;

        .title {

          .title_main {
            font-size: 45px;
            text-shadow: 5px 5px 6px #282626;
          }
        }
      }

      .era_counter_tap {
        border: 1px solid #f5ff00;
      }
    }
  }
}

@media (max-width: 670px) {
  .game_out {

    .game_in{
      .instructions_right{

        .instructions_right_story{

          .instructions_right_story_txt{

            p{
              font-size: 12px;
            }
          }

          .instructions_right_story_next{
            width: 60px;
            height: 20px;
            font-size: 12px;
          }
        }

        .instructions_right_era{

          .instructions_right_era_txt{

            p{
              font-size: 12px;
            }
          }

          .instructions_right_era_next{
            width: 60px;
            height: 20px;
            font-size: 12px;
          }
        }
      }
    }
  }
}

@media (max-width: 640px) {
  .game_out {

    .game_in {

      .title_story {

        .title {

          .title_main {
            font-size: 40px;
            -webkit-text-stroke-width: 1px;
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .game_out {

    .game_in {

      .instructions_left {

        p {
          font-size: 12px;
        }
      }

      .instructions_right {

        p {
          font-size: 12px;
        }
      }
    }
  }
}

@media (max-width: 580px) {
  .game_out {

    .game_in {

      .title_story {

        .title {

          .title_main {
            font-size: 30px;
          }
        }
      }
    }
  }
}
