.game{
  background-image: linear-gradient(45deg, #001f61, #0091d1);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  -webkit-tap-highlight-color: rgba(0,0,0,0);

  .end {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-image: url("../images/6finish.jpg");
    background-size: cover;
    background-position: 50% 50%;

    .end_txt {
      font-family: 'Bangers', cursive;
      color: #f5ff00;
      font-size: 65px;
      letter-spacing: 1px;
      text-shadow: 5px 5px 10px #282626;
      -webkit-text-stroke-width: 3px;
      -webkit-text-stroke-color: #000656;
    }

    .footer{
      background-color: rgba(0, 6, 86, 0.76);
      border-radius: 10px 10px 0 0;
      color: #f5ff00;
      font-size: 10px;
      position: fixed;
      bottom: 0;
      left: 50%;
      transform: translate(-50% , 0);
      margin: 0;
      padding: 0 5px 0 5px;

      a{
        text-decoration: none;
        color: #f5ff00;

        &:hover{
          text-shadow: 0 0 4px #f5ff00;
        }
      }
    }

    .restart{
      font-family: 'Sriracha', cursive;
      width: 110px;
      height: 50px;
      color: #f5ff00;
      background-color: #002c88;
      font-size: 18px;
      text-transform: uppercase;
      border-radius: 30px;
      border: 3px solid #f5ff00;
      box-shadow: 5px 5px 10px #282626;
      transition: 0.4s all;
      cursor: pointer;
      -webkit-tap-highlight-color: rgba(0,0,0,0);

      &:hover{
        color: #002c88;
        background-color: #f5ff00;
        border: 3px solid #002c88;
      }
    }
  }

  .width_control{
    font-family: 'Sriracha', cursive;
    width: 100%;
    height: 100%;
    position: absolute;
    background-image: linear-gradient(45deg, #0032af, #006b85);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    visibility: hidden;

    p{
      font-size: 20px;
      text-transform: uppercase;
      color: #f5ff00;
    }

    img{
      width: 100px;
      height: 100px;
    }
  }
}

@media (max-width: 600px) {
  .game{

    .end {

      .end_txt{
        font-size: 55px;
      }

      .restart{
        width: 70px;
        height: 30px;
        font-size: 12px;
        border: 2px solid #f5ff00;

        &:hover{
          border: 2px solid #002c88;
        }
      }
    }
  }
}

@media (max-width: 450px) {
  .game{
    .width_control{
      visibility: visible;
    }
  }
}
